.modal-state .modal {
  @media (max-width: 768px) {
    margin: 0 16px;
    width: calc(100% - 32px);
    left: 0;
    transform: translate(0%, -50%);
  }
}

.modal-state-message {
  display: flex;
  flex-direction: column;
  row-gap: 32px;
  align-items: center;
  margin: 0 70px;

  @media (max-width: 768px) {
    margin: 26px 20px auto;
  }
}

.modal-state .label {
  font-size: 21px;
  font-weight: 600;
  font-family: inherit;
  margin: 0;
  text-align: center;
}

.modal-state .label.message {
  font-size: 14px;
  font-weight: 400;
  margin-top: -14px;
}

.modal-buttons {
  display: flex;
  flex-direction: row;
  column-gap: 16px;
  width: 100%;

  @media (max-width: 768px) {
    flex-direction: column;
    row-gap: 16px;
  }
}

.modal-buttons > * {
  width: 100%;
}