.captured-container {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9998;
  column-gap: 110px;
  justify-content: center;
  align-items: center;
  min-height: 370px;

  @media (max-width: 768px) {
    align-items: center;
    column-gap: 38px;
    min-height: 100px;
  }
}

.opacity {
  background-color: var(
    --UI--colors-primary400,
    var(--video-contracting-color-primary)
  );
  opacity: 0.2;
  border-color: #ffffff;
  border-style: dashed;
  border-width: 8px;

  @media (max-width: 768px) {
    border-width: 3px;
  }
}

.captured-face {
  border-radius: 50%;
  aspect-ratio: 3/4;
  height: 80%;
  margin-top: 32px;

  @media (max-width: 768px) {
    height: 240px;
    max-height: 240px;
    margin-top: 0;
  }
}

.captured-document {
  border-radius: 32px;
  aspect-ratio: 1.59/1;
  height: 30%;
  margin: auto 0;

  @media (max-width: 768px) {
    height: 70px;
    border-radius: 16px;
  }
}