.container {
  display: flex;
  flex-direction: column;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  opacity: 1;
  z-index: 9999;
  transition: z-index 0.5s ease-in-out, opacity 0.5s ease-in-out;
  overflow-y: auto;
  position: initial;

  @media (max-width: 768px) {
    position: fixed !important;
  }
}

.container.full-screen {
  position: fixed;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 16px;

  background-color: #10192B;
  height: 80px;
  min-height: 80px;
  padding: 0 26px 0 32px;

  @media (max-width: 768px) {
    height: 64px;
    min-height: 64px;
    padding: 0 24px;
  }
}

.header-time {
  display: flex;
  column-gap: 8px;
  flex: 0 0 125px;
}

.label-time {
  font-family: inherit;
  font-weight: 400;
  margin: 0;
  font-size: 10px;
  color: white;
}

.bullet {
  height: 11px;
  aspect-ratio: 1;
  border-radius: 50%;
  background: #eb184c;
}

.footer {
  display: flex;
  align-items: center;
  min-height: 72px;
  padding: 0 16px;
  background-color: #10192B;

  @media (min-width: 769px) {
    display: none;
  }
}

.header .instructions-container {
  @media (max-width: 768px) {
    display: none;
  }
}