.modal-start {
  box-sizing: border-box;
}

.modal-start .modal {
  @media (max-width: 768px) {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    transform: unset;
    border-radius: 0;
    padding: 24px 20px;
    max-height: 100%;
  }
}

.modal-start-message {
  display: flex;
  flex-direction: column;
  row-gap: 32px;
  align-items: center;
  margin: 0 70px;

  @media (max-width: 768px) {
    margin: 26px 20px auto;
  }
}

.modal-start .label {
  font-family: inherit;
  font-weight: 400;
  margin: 0;
  font-size: 16px;
  text-align: center;
  color: black;
}

lottie-animation > div {
  height: 250px;
}