.video-container {
  flex: 1;
  background: #243760;
  position: relative;
}

.video-player {
  position: relative;
  display: flex;
  justify-content: center;
  width: 100%;
  height: calc(100vh - 80px);
  align-items: center;
  overflow: hidden;

  @media (max-width: 768px) {
    height: calc(100vh - 136px);
  }
}

video {
  transform: rotatey(180deg);
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}